.pisende.container-fluid {
  padding-right:0;
  padding-left:0;
  margin-right:auto;
  margin-left:auto;
}

.espacio {
  height: 432px;
  background-size: cover;
  background-position: center center;
  position: relative;

  @media screen and (min-width: 768px){
    height: 720px;
  }

  @media screen and (min-width: 992px){
    height: 100vh;
  }

  .espacio__opciones {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 60px;
    background:#AA0635;

    .opcion__icono {
      width: 24px;
    }
    .opcion__titulo {
      padding-top: 7px;
      color: #fff;
      font-size: 13px;
      font-family: 'nexa',sans-serif;
      letter-spacing: 2px;
      margin-left: 3px;
    }
  }
}

.espacio__menu {
  padding-top: 36px;
  padding-bottom: 36px;

  @media screen and (min-width: 992px){
    padding-top: 150px;
  }

  h3 {
    color: #009966;
    font-size: 16px;
    font-family: 'nexa', sans-serif;
    letter-spacing: 1px;
    padding-left: 30px;
    margin-bottom: 24px;
    text-align: center;

    @media screen and (min-width: 992px){
      text-align: left;
      margin-bottom: 50px;
    }
  }

  ul.nav {
    padding: 0;
    margin: 0;

    .nav-item {
      padding-left: 30px;
      padding-right: 30px;
      transition: all .3s;
    }

    .nav-link {
      padding-right: 0;
      padding-left: 0;

      font-size: 14px;
      font-family: 'nexa', sans-serif;
      color: #4D4D4D;
      letter-spacing: 3px;
    }
  }

}

.espacio__thumbnails {

  padding: 36px 24px;

  @media screen and (min-width: 992px){
    padding-top: 150px;
    height: 100vh;
  }

  h3 {
    color: #008165;
    font-size: 16px;
    font-family: 'nexa', sans-serif;
    letter-spacing: 1px;
    margin-bottom: 24px;

    @media screen and (min-width: 992px){
      margin-bottom: 50px;
    }
  }

  .galeria_wrap {

    @media screen and (min-width: 992px){
      max-height: 85%;
      overflow-y: scroll;
      margin-right: 1%;
    }

  }

  .galeria {



    .galeria__item {
      span {
        font-size: 14px;
        font-family: 'nexa', sans-serif;
        color: #4D4D4D;
        letter-spacing: 3px;
      }

      @media screen and (max-width: 991px){
        margin-right: 12px;
        width: 90px;
        flex-wrap: nowrap;
      }
    }

  }
}

.bg-gray {
  background: #f4f4f4;
}

/* width */
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f4f4f4;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ccc;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}